.producto {
  border: 0.5px solid #ddd;
  padding: 20px;
  width: 33%;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
}

.producto img{
  width: 200px;
}
.producto h5{
  text-align: center;
}

.producto h5 span{
  background-color: rgb(255, 178, 178);
  padding: 3px;
  font-weight: lighter;
  color: rgb(155, 0, 0);
  border-radius: 3px;
}
.producto button{
  padding: 5px;
  text-transform: uppercase;
}